<template>
  <div>
    <v-data-table
      v-model="selected"
      :headers="headers"
      :items="items"
      class="elevation-1"
      item-key="uuid"
      show-select
      :footer-props="tableProps.FOOTER_PROPS"
      :itemsPerPage="tableProps.ITEMS_PER_PAGE"
      :search="search"
    >
      <template v-slot:top >
        <v-toolbar flat>
          <v-toolbar-title> LoRa Network </v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <div class="text-left pa-2">
            <v-btn @click="onClickRefresh" x-small color="primary" class="white--text" >Refresh <v-icon x-small right dark> mdi-refresh </v-icon></v-btn>&nbsp;
            <v-btn @click="dialog = true; currentItem = null" x-small color="success" class="white--text" >Create <v-icon x-small right dark> mdi-plus </v-icon></v-btn>&nbsp;
            <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text">Delete <v-icon x-small right dark > mdi-delete </v-icon></v-btn>&nbsp;
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn x-small color="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
              </template>
              <v-list>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(items, 'json')">Json</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(items, 'excel')">Excel</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="clickDownload(items,'csv')">CSV</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>&nbsp;
            <v-menu offset-y>
              <template v-slot:activator="{ on, attrs }">
                <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on" >Import<v-icon x-small right dark>mdi-import</v-icon></v-btn>
              </template>
              <v-list>
                <v-list-item link>
                  <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                </v-list-item>
                <v-list-item link>
                  <v-list-item-title @click="dialogImportExcel = true">Excel</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>&nbsp;
            <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" > Export <v-icon x-small right dark> mdi-export </v-icon> </v-btn>
            <v-btn @click="onClickOpenPointsView()" x-small color="deep-purple" class="white--text" > View All Points<v-icon x-small right dark> mdi-view-headline </v-icon> </v-btn>&nbsp;
          </div>
          <v-spacer></v-spacer>
          <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
        </v-toolbar>
      </template>
      <template v-slot:item.name="{ item }">
        <a @click="onClickOpen(item)"> {{ item.name }} </a>
      </template>

      <template v-slot:item.enable="{ item }">
        <v-checkbox v-model="item.enable" @click="onSave(item)"></v-checkbox>
      </template>

      <template v-slot:item.id="{ item }">
        <v-edit-dialog :return-value.sync="item.id" @save="onSave(item)" @cancel="cancelEdit" @open="openEdit(item)" @close="closeEdit" large >
          <b>{{ item.id }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.id" single-line required :rules="[() => !isExistingvalue(item.id, 'id') || 'id Existed', rules.required]"></v-text-field>
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:item.device_type="{ item }">
        <v-edit-dialog :return-value.sync="item.device_type" @save="onSave(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
          <b>{{ item.device_type }}</b>
          <template v-slot:input>
            <v-select v-model="item.device_type" :items="['DROPLET', 'MICRO_EDGE']"></v-select>
          </template>
        </v-edit-dialog>
      </template>


      <template v-slot:item.device_model="{ item }">
        <v-edit-dialog :return-value.sync="item.device_model" @save="onSave(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
          <b>{{ item.device_model }}</b>
          <template v-slot:input>
            <v-select v-model="item.device_model" :items="['MICRO_EDGE', 'DROPLET_TH', 'DROPLET_THL', 'DROPLET_THLM', 'DROPLET_THA', 'DROPLET_THLA']"></v-select>
          </template>
        </v-edit-dialog>
      </template>


      <template v-slot:item.ai_1_config="{ item }">
        <v-edit-dialog :return-value.sync="item.ai_1_config" @save="onSave(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
          <b>{{ item.ai_1_config }}</b>
          <template v-slot:input>
            <v-select v-model="item.ai_1_config" :items="['TEMP', 'VOLTAGE', 'DIGITAL', 'RAW']"></v-select>
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:item.description="{ item }">
        <v-edit-dialog :return-value.sync="item.description" @save="onSave(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
          <b>{{ item.description }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.description" single-line></v-text-field>
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:header.enable="{ item }" >
        <div class="d-flex">
          <span>{{ toUpperTextKey('enable') }}</span>&nbsp;&nbsp;
          <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>
        </div>
      </template>

      <template v-slot:header.device_type="{ item }" >
        <div class="d-flex">
          <span>Sensor Type</span>&nbsp;&nbsp;
          <v-icon x-small @click="openMassEditDialog('device_type')" color="primary">mdi-pencil</v-icon>
        </div>
      </template>

      <template v-slot:header.device_model="{ item }" >
        <div class="d-flex">
          <span>Sensor Model</span>&nbsp;&nbsp;
          <v-icon x-small @click="openMassEditDialog('device_model')" color="primary">mdi-pencil</v-icon>
        </div>
      </template>

      <template v-slot:header.description="{ item }" >
        <div class="d-flex">
          <span>{{ toUpperTextKey('description') }}</span>&nbsp;&nbsp;
          <v-icon x-small @click="openMassEditDialog('description')" color="primary">mdi-pencil</v-icon>
        </div>
      </template>

      <template v-slot:item.actions="{ item }" >
        <v-icon small class="mr-2" @click="editItem(item)" color="primary">
          mdi-pencil
        </v-icon>
        <v-icon small @click="clickDelete(item)" color="error">
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:no-data> No data </template>
      <template v-slot:item.created_on="{ item }" >
        {{item.created_on | timezone}}
      </template>
      <template v-slot:item.updated_on="{ item }" >
        {{item.updated_on | timezone}}
      </template>
    </v-data-table>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Create/edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <ln-sensor-form :item="currentItem" :onCancel="onCancel" v-if="dialog" :onSave="onSave"></ln-sensor-form>
    </v-dialog>

    <!-- Import Json dialog-->
    <v-dialog v-model="dialogImport" >
      <v-toolbar flat dark >
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport" >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1" > Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2" > Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport" ></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>
          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="headers"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
              >
                <template v-slot:top >
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small @click="clickDeleteImport(item)" color="error"> mdi-delete </v-icon>
                </template>
                <template v-slot:item.name="{ item }" >
                  <v-text-field v-model="item.name" single-line style="width:100px" required :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]"></v-text-field>
                </template>
                <template v-slot:item.id="{ item }" >
                  <v-text-field v-model="item.id" type="string" single-line style="width:100px" required :rules="[() => !isExistingvalue(item.id, 'id') || 'id Existed', rules.required]"></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog :is-open="dialogImportExcel" :on-close="()=>dialogImportExcel = false" :on-confirm="convertExcelToJson"></import-from-excel-dialog>


    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-select v-if="editField === 'device_type'" v-model="editAll[editField]" :items="['DROPLET', 'MICRO_EDGE']" :label="toUpperTextKey(editField)" ></v-select>
          <v-select v-if="editField === 'device_model'" v-model="editAll[editField]" :items="['MICRO_EDGE', 'DROPLET_TH', 'DROPLET_THL', 'DROPLET_THLM', 'DROPLET_THA', 'DROPLET_THLA']" :label="toUpperTextKey(editField)" ></v-select>
          <v-select v-if="editField === 'ai_1_config'" v-model="editAll[editField]" :items="['TEMP', 'VOLTAGE', 'DIGITAL', 'RAW']" :label="toUpperTextKey(editField)" ></v-select>
          <v-select v-if="editField === 'ai_1_config'" v-model="editAll[editField]" :items="['TEMP', 'VOLTAGE', 'DIGITAL', 'RAW']" :label="toUpperTextKey(editField)" ></v-select>
          <v-select v-if="editField === 'ai_1_config'" v-model="editAll[editField]" :items="['TEMP', 'VOLTAGE', 'DIGITAL', 'RAW']" :label="toUpperTextKey(editField)" ></v-select>
          <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
          <v-text-field v-if="typeof editAll[editField] === 'string' && editField !== 'device_model' && editField !== 'device_type'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
import axios from "../../axios";
import * as xlsx from "xlsx";
import * as fileImportExportService from '../../services/fileImportExportService';
import * as changeCase from "change-case";
import _cloneDeep from 'lodash/cloneDeep';
import _find from 'lodash/find';
import _forIn from 'lodash/forIn';
import LnSensorForm from './LnSensorForm';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { RULES, DEVICE_HEADERS, DEVICE_EDIT_ALL_DEFAULT } from './loraConstant';
import { exportCSV, isJsonString, copyToClipboard, exportExcel, exportJSON, toUpperTextKey } from "../../utils/utils";

export default {
  components: {
    LnSensorForm, ConfirmDialog, ImportFromExcelDialog,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    rules: {...RULES},
    tableProps: {...DATA_TABLE_PROPS},
    headers: [...DEVICE_HEADERS],
    editAll: {...DEVICE_EDIT_ALL_DEFAULT},
    dialog: false,
    dialogImportExcel: false,
    dialogImport: false,
    dialogDelete: false,
    dialogMassEdit: false,
    version: 1,
    action: "",
    selected: [],
    info:{},
    items: [],
    e1: 1,
    dataImport: "",
    valid: false,
    importArray: [],
    search:"",
    searchWizard:"",
    editField: "",
    intervalId: null,

  }),
  computed: {
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  async mounted() {
    this.getItems();
  },
  methods: {
    editItem(item) {
      this.currentItem = item;
      this.dialog = true;
    },
    onCancel() {
      this.dialog = false;
    },
    async onSave(data) {
      let postData = {...data};
      const method = data.uuid ? 'patch': 'post';
      let url = `lora/device`;
      if (data.uuid) {
        url += '/' + data.uuid;
        delete postData.uuid;
      }
      postData.name = this.setting.enableChangeCase ? changeCase[this.setting.caseType](postData.name) : postData.name;
      axios[method](url, postData)
        .then(async value => {
          this.dialog = false;
          if(method == 'patch'){
            const index = this.items.findIndex(i=> i.uuid == data.uuid);
            Object.assign(this.items[index], postData);
          }
          // await this.getItems();
          this.$toasted.show("success");
        });

    },
    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.items.indexOf(this.currentItem);
      this.items.splice(this.editedIndex, 1);
      await axios.delete(`lora/device/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    async getItems() {
      this.items = await axios.get('lora/device');
    },
    clickDownload(array, type){
      // const data = this.convertExportData(array);
      const data = fileImportExportService.prepareFileExport(array);
      if(type === "csv"){
          return exportCSV("LoRa Networks - Devices", data);
      } if(type === "excel"){
          return exportExcel("LoRa Networks - Devices", data);
      } if(type === "json"){
          return exportJSON("LoRa Networks - Devices", JSON.stringify(this.items));
      }
    },
    // convertExportData(devices){
    //   const data = _cloneDeep(devices).map((device)=>{
    //     _forIn(device, (device_value, device_key) => {
    //       if(device_key == 'sensor_store'){
    //         _forIn(device[device_key], (sub_value, sub_key) => {
    //           const subKey = `sensor_store.${sub_key}`;
    //           device = {...device, [subKey] : sub_value}
    //         });
    //         delete device[device_key];
    //       }
    //     });
    //     return device;
    //  });
    //   return data;
    // },
    async saveImport() {
      if(this.importArray && this.importArray.length > 0){
        if(this.$refs.form.validate()){
          for (const item of this.importArray) {
            delete item.uuid;
            await this.onSave(item);
          }
          this.closeDialogImport();
          this.selected = [];
          this.getItems();
        }
      }
    },
    clickContinue(){
      if(!isJsonString(this.dataImport) ){
        return this.$toasted.show("incorrect data type!", { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport);
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      const devices = _cloneDeep(this.items);
      const index = this.items.indexOf(this.currentItem);
      if(index !== -1){ //when edit
        devices.splice(index, 1);
      }
      if(fieldName === "id"){
        value = String(value)
      }
      return _find(devices, {[fieldName]: value}) != null;;
    },
    clickImportJson(){
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : "";
      this.e1 = 1;
    },
    convertExcelToJson(file){
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = (event)=>{
        let data = event.target.result;
        let workbook = xlsx.read(data,{type:"binary"});
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map((i) => {
            for(let key in i){
              const newKey = key.trimRight("");
              const value = i[key];
              delete i[key];
              i = {...i, [newKey]: value}
            }
            return i;
          });
          this.dataImport = fileImportExportService.prepareFileImport(items);
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      }
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.$nextTick(() => {
        this.dataImport = "";
      });
    },
    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    onClickOpen(item) {
      clearInterval(this.intervalId);
      this.$router.push({ path: `/lora-network/devices/${item.uuid}/points`  });
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.onSave(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = {...DEVICE_EDIT_ALL_DEFAULT};
    },
    cancelEdit() { },
    openEdit(item) {
      this.currentItem = item;
    },
    closeEdit() {
      this.currentItem = {};
    },
    prepareToExit: function() {
      clearInterval(this.intervalId)
    },
    autoRefresh: function(){
      //set auto refresh
      if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
        this.intervalId = setInterval(()=>{
          this.getItems();
          if(this.setting.toastAutoRefresh){
            this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
          }
        }, this.setting.refreshTime);
      }
    },
    onClickRefresh(){
      this.getItems();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    onClickOpenPointsView(){
      this.$router.push({ path:`/lora-network/points-view` });
    },
  }
};
</script>
<style scoped>
.config-server{
  position:absolute;
  right: 1em;
}
@media only screen and (max-width: 600px) {
  .config-server{
    bottom: -3em;
    left: 8.2rem;
  }
}
</style>
