<template>
  <div>
    <v-card>
      <v-card-title>
        <v-btn @click="importDialog = true, dataImport = ''" x-small color="primary" class="white--text">
          Import<v-icon x-small right dark>mdi-import</v-icon>
        </v-btn>&nbsp;
        <v-btn @click="clickJsonExport" x-small color="#009688" class="white--text" >
          Export<v-icon x-small right dark > mdi-export </v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-form v-model="valid" ref="form" lazy-validation>
            <v-row>
              <v-col cols="12" sm="6" md="4">
                <v-text-field v-model="info.port" label="Port" required :rules="[rules.required]"></v-text-field>
              </v-col>
                <v-col cols="12" sm="6" md="4">
                <v-select
                  v-model="info.baud_rate"
                  label="Baud Rate"
                  :items="[110, 300, 600, 1200, 2400, 4800, 9600, 14400, 19200, 38400, 57600, 115200, 128000, 256000]"
                  required :rules="[rules.required]"></v-select>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field type="number" v-model="info.stop_bits" label="Stop Bits" required :rules="[rules.required]"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-select
                  v-model="info.parity"
                  label="Parity"
                  :items="['O', 'E', 'N', 'Odd', 'Even']"
                  required :rules="[rules.required]"></v-select>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field type="number" v-model="info.byte_size" label="Byte Size" required :rules="[rules.required]"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="4">
                <v-text-field type="number" v-model="info.timeout" label="Timeout" required :rules="[rules.required]"></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" :disabled="!valid" text @click="onPortSave(info)"> Save </v-btn>
      </v-card-actions>
    </v-card>

    <!--Import dialog -->
    <v-dialog v-model="importDialog" max-width="500px">
      <v-card>
        <v-card-title> Import </v-card-title>
        <v-card-text>
          <v-container>
            <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="importDialog = false">Cancel</v-btn>
          <v-btn color="blue darken-1" text v-on:click="saveImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>

</template>

<script>
import { isJsonString, copyToClipboard } from "../../utils/utils";
import axios from "../../axios";
import { RULES } from './loraConstant';


export default {
  components: {
  },
  data: () => ({
    rules: {...RULES},
    version: 1,
    action: "",
    info:{},
    valid: false,
    importDialog: false,
    dataImport: "",
  }),
  watch: {
  },
  async mounted() {
    this.getInfo();
  },
  methods: {
    async getInfo() {
      this.info = await axios.get(`lora/networks`);
    },
    async onPortSave(data) {
      if(this.$refs.form.validate()){
        await axios.patch(`lora/networks`, data).then(r => {
          this.$toasted.show('Success');
          this.importDialog = false;
          this.getInfo();
        })
      }

    },
    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.info));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    async saveImport(){
      if(!isJsonString(this.dataImport)){
        return this.$toasted.show("incorrect data type!", {type:'error'});
      }
      const importData = JSON.parse(this.dataImport);
      this.onPortSave(importData);
    },
  }
};
</script>
<style scoped>
.config-server{
  position:absolute;
  right: 1em;
}
@media only screen and (max-width: 600px) {
  .config-server{
    bottom: -3em;
    left: 8.2rem;
  }
}
</style>
