<template>
  <div>
    <v-tabs v-model="tab" right dark>
      <v-tab :key="0"> LoRa Network </v-tab>
      <v-tab :key="1"> Serial Networks </v-tab>
      <v-tab :key="2"> Debug Messages </v-tab>
      <v-tab :key="3"> System Status </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <ln-device-table ref="loraTable"></ln-device-table>
      </v-tab-item>
      <v-tab-item :key="1">
        <ln-serial-form></ln-serial-form>
      </v-tab-item>
      <v-tab-item :key="2">
        <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
      </v-tab-item>
      <v-tab-item :key="3">
        <system-status-table server='lora'></system-status-table>
      </v-tab-item>
    </v-tabs-items>

  </div>
</template>

<script>
import axios from "../../axios";
import LnDeviceTable from './LnDeviceTable';
import LnSerialForm from './LnSerialForm';
import MqttConsole from '../Mqtt/MqttConsole';
import SystemStatusTable from '../Common/SystemStatusTable.vue';

export default {
  components: {
    LnSerialForm,
    MqttConsole,
    LnDeviceTable,
    SystemStatusTable,
  },
  data: () => ({
    topic: 'rubix/lora_raw/raw',
    tab: 0,
    tabs: ['table', 'port', 'debug-message', 'system'],
  }),
  computed: {
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.lora;
    },
  },
  beforeRouteLeave (to, from, next) {
    this.$refs.loraTable.prepareToExit();
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).finally(() => {
        next();
      });
  },
  watch: {
    '$route.query.tab':function(val){
      this.tab = this.tabs.indexOf(val);
    },
    tab: function (val, oldVal) {
      if(oldVal == 0){
        this.$refs.loraTable.prepareToExit();
      }
      if(val == 0){
        this.$refs.loraTable.autoRefresh();
      }
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: '/lora-network/devices', query: { tab: tabKey } });
      }
    },
  },
  mounted(){
    const { tab: tabKey } = this.$route.query;
    this.tab = this.tabs.indexOf(tabKey);
  },
  methods:{
    // getSystemStatus() {
    //   axios.get(`lora/system/ping`).then(data => {
    //     console.log(data)
    //   });
    // }
  },
};
</script>
